<script>
import anime from 'animejs/lib/anime.es.js';

export default {
 methods: {
        isInViewport(element) {
            if(!element) return
            if(element) {
            const rect = element.getBoundingClientRect();
        
            return (
            rect.top >= 0 &&
            rect.left >= 0 &&
            rect.bottom <=
                (window.innerHeight || document.documentElement.clientHeight) &&
            rect.right <=
                (window.innerWidth || document.documentElement.clientWidth)
            );
            }
        
        },
        checkIf() {
            let lines = ['line1','line2','line3','line4','line5']
            lines.forEach((line) => {
                let lineEl = document.querySelector('#'+line);
                if(this.isInViewport(lineEl)) {
                    window.removeEventListener('scroll', this.checkIf);
                    anime({
                        targets: ['#line1', '#line2', '#line3', '#line4', '#line5'],
                        strokeDashoffset: [anime.setDashoffset, 0],
                        easing: 'easeInOutSine',
                        duration: 3500,
                        delay: function(el, i) { return i * 250 },
                        direction: 'normal',
                    });
                }
            })
        }
    },
    computed: {
        darkMode() {
            return {
                color: this.$store.state.dark ? "white" : "#0b090a",
                fill: this.$store.state.dark ? "white" : "#0b090a",
            };
        },
    },
    mounted() {
        window.addEventListener('scroll', this.checkIf())
        window.addEventListener('scroll', () => {
            if (this.isInViewport(this.$refs.line)) {
                this.$store.commit("toggleDarkMode", true);
            } 
        })
    },
}
</script>

